




















import Vue from 'vue'

export default Vue.extend({
  props: {
    position: {
      type: String,
      default: 'right'
    },
    options: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      showMenu: false
    }
  },
  methods: {
    toggleMenu(): void {
      this.showMenu = !this.showMenu
    },
    handleSelect(option: string): void {
      this.$emit('select', option)
      this.showMenu = false
    }
  }
})
